<template>
    <!-- 公告 -->
    <div class="heigth-div scrollBar">
        <div class="notice-div" v-for="(item,index) in items" :key="index">
            <span class="spanI">{{item.title}}</span>
            <span class="spanII">{{item.receivetime}}</span>
        </div>
        <!-- <div class="notice-div">
            <span class="spanI">关于系统升级公告-智教学企业平台</span>
            <span class="spanII">2017-12-21</span>
        </div> -->
    </div>

</template>

<script>
    // import ZLCommonTable from '../../../../components/ZLCommonTable'
    export default {
        // components:{
        //     ZLCommonTable,
        // },
        data() {
            return {
                items:[]
            }
        },

        watch: {
            "$store.state.noticnum":"getlist"
        },

        mounted() {
            this.init();
        },

        methods: {
            init() {
                //this.initEchart();
               // this.setEchart();
               // this.getlist();
            },

            getlist(){
                const _this = this;
                this.fetch('/message/inbox/allByPage',{inbox:{status:0,type:"xt"}})
                .then(result => {
                    if(result.status === 200){
                        this.items = result.data.list
                        //this.$store.state.noticnum = result.data.total;
                    }
                })

            },
        }
    }
</script>

<style lang="less" scoped type="text/less">
    .heigth-div{
        height: 95%;
        overflow-y:auto
    }
    .test-1::-webkit-notice-div {/*滚动条整体样式*/
        width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
    }
    .notice-div{
        margin-top: 10px;
        margin-left: 20px;
        display: flex;
        justify-content: space-between;
        .spanII{
            margin-right: 10px;
        }
    }
</style>



